@import '../../style/main.scss';

.topBanner {
    background-color: $color1;
    padding: 10px 30px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    color: white;

    .contactInfo {
        display: flex;
        flex-direction: row;
        align-items: center;
        text-align: right;

        .email {
            
            text-decoration: none;
            color: white;

            &:hover {
                text-decoration: underline;
            }

            .mailIcon {
                width: 20px;
                height: 20px;
                transform: translateY(25%);
                margin-right: 8px; /* Espace entre l'icône et le texte */
            }
        }

        .address {
            margin: 5px 25px;
            .mailIcon {
                width: 20px;
                height: 20px;
                transform: translateY(25%);
                margin-right: 8px; /* Espace entre l'icône et le texte */
            }
        }

        .socialIcons {
            display: flex;
            gap: 10px;
            margin-top: 10px;

            a {
                display: inline-block;
                width: 30px; /* Taille du cercle */
                height: 30px;
                background-color: #fff; /* Fond blanc */
                border-radius: 50%; /* Cercle parfait */
                display: flex;
                justify-content: center;
                align-items: center;

                img {
                    width: 20px; /* Taille de l'icône */
                    height: 20px;
                }

                &:hover {
                    box-shadow: 0 0 40px $color2; /* Ombre plus prononcée au hover */
                }
            }
        }
    }
}
